.apps {
  text-align: center;
}

img{
  width : 70%;
  height: 166px;
}
.envelope{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.envelope  div{
  cursor: pointer;
}
.card{
  width: 250px;
   height: 340px;
}
.card-body p {
  text-align: left;
}

.containers{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 90vw;
  height: 90vh;
}
.flag{
  width: 50%;
  margin-left: 4rem;
}
.back{
  margin-left: 4rem;
  text-decoration: none;
  padding: .6rem 1.3rem;
   position: relative;
   top: 30px;
   font-weight: bold;
   background: #FFFFFF;
box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.05);
}
.container .details{
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  width: 45%;
}
.first h1{
  margin-top: 0;
}
.header{
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  position: relative;
  align-items: center;
}
.header input{
  width: 34%;
  height: 50px;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.05);
  border-style: none;
  padding-left: 2rem;
}
.header select{
  height: 40px;
}
.svg{
  position: absolute;
  top: 25%;
  left: 1%;
}

@media (max-width: 466px) {
.header {
  flex-direction: column;
}
 .header input{
   width: 100%;
 }
 .svg{
   top: 10%;
 }
 .header select{
   align-self: flex-start;
   width: 50%;
   margin-top: 2rem;
 }
 .main-header h1{
   font-size: 20px;
   text-align: left;
   margin-bottom: 1rem;
   padding-top: 1rem;
 }
  .card{
   width: 300px; 
   height: 300px;
  } 
  .mb-4{
      display: flex;
      justify-content: center;
  }
  .envelope div{
   height: 350px;
  }
  .containers{
    flex-direction: column;
    margin-top: 6rem;
    width: 100%;
    align-items: flex-start;
    padding-left: 1rem
  }
  .containers .details{
    flex-direction: column;
    text-align: left;
    justify-content: left;
    width: 90%;
  }
  .flag{
    width: 100%;
    margin-left: 0;
    margin-bottom: 2rem;
  }
  .back{
    margin-left: 0;
    text-decoration: none;
   margin-left: 1rem
  }
  
  .first{
    margin-bottom: 2rem;
  }
  img{
    width: 95%;
  }
 }
 .back:hover{
  text-decoration: none;
}